@font-face {
    font-family: 'Neue Haas Unica';
    src: local('Neue Haas Unica Light'), local('Neue-Haas-Unica-Light'),
        url('../../dist/fonts/NeueHaasUnica-Light.woff2') format('woff2'),
        url('../../dist/fonts/NeueHaasUnica-Light.woff') format('woff'),
        url('../../dist/fonts/NeueHaasUnica-Light.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
  }
  @import url('https://fonts.googleapis.com/css2?family=El+Messiri:wght@400;500;600;700&family=Mrs+Saint+Delafield&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Neue Haas Unica';
}
// HERO
.hero {
    background: url('../../dist/img/hero_bg.jpg') no-repeat;
    background-size: cover;
    width: 100%;
    height: 100vh;
    padding: 55px 10px;
    .navbar-brand {
        font-family: 'Neue Haas Unica';
        font-style: normal;
        font-weight: 900;
        font-size: 22px;
        line-height: 17px;
        letter-spacing: 0.08em;
        color: #FFFFFF;
        margin-right: 90px;
    }
    .nav-item {
        .nav-link {
            font-family: 'Neue Haas Unica';
            color: #FFFFFF;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 17px;
            transition: all .3s linear;
            border-radius: 10px;
            margin: 4px;
            &:hover {
                background: #00D664;
            }
            &.active {
                background: #00D664;
            }
        }
    }
    .btn {
        font-family: 'Neue Haas Unica';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 17px;
        text-align: center;
        color: #FFFFFF;
        background: #00D664;
        border-radius: 27.5px;
        padding: 15px 35px;
        &:hover {
            background: transparent;
            border: 1px solid #00D664;

        }
    }
    .navbar-toggler {
        outline: none;
        border: none;
    }
    .hero_text {
        max-width: 795px;
        margin: 100px auto;
        .hero_subtitle {
            font-family: 'Mrs Saint Delafield';
            font-style: normal;
            font-weight: 400;
            font-size: 58px;
            line-height: 17px;
            color: #FFFFFF;
        }
        .hero_title {
            font-style: normal;
            font-weight: 500;
            font-size: 60px;
            line-height: 70px;
            text-align: center;
            letter-spacing: 0.02em;
            color: #FFFFFF;
        }
        .hero_suptitle {
            font-weight: 400;
            font-size: 18px;
            line-height: 27px;
            text-align: center;
            letter-spacing: 0.02em;
            color: #FFFFFF;
            max-width: 540px;
            margin: 35px auto;
        }
    }
}
// ABOUT
.about {
    padding: 120px 10px 100px;
    .about_subtitle {
        font-family: 'Mrs Saint Delafield';
        font-style: normal;
        font-weight: 400;
        font-size: 58px;
        line-height: 27px;
        color: #ACACAC;
        margin-bottom: 25px;
    }
    .about_title {
        font-weight: 500;
        font-size: 42px;
        line-height: 58px;
        color: #1E266D;
        margin-bottom: 25px;
    }
    .about_suptitle {
        font-weight: 400;
        font-size: 18px;
        line-height: 27px;
        letter-spacing: 0.03em;
        color: #696969;
        max-width: 820px;
        margin: 25px auto;
    }
    .about_left {
        padding: 80px 40px 40px 0;
    }
    .about_left_title {
        font-weight: 500;
        font-size: 28px;
        line-height: 36px;
        color: #1E266D;
        margin-bottom: 45px;
    }
    .about_item_title {
        font-weight: 500;
        font-size: 20px;
        line-height: 44px;
        color: #1E266D;

        span {
            margin-right: 35px;
        }
    }
    .about_item_desc {
        font-weight: 400;
        font-size: 18px;
        line-height: 28px;
        letter-spacing: -0.01em;
        color: #696969;
        margin-left: 80px;
    }
}
// SERVICES
.services {
    padding: 160px 10px 100px;
    background: #F7F4FB;
    .services_title {
        font-weight: 500;
        font-size: 34px;
        line-height: 58px;
        color: #1E266D;
        margin-bottom: 70px;
    }
    .row {
        margin-bottom: 70px;
        .services_item {
            margin-top: 15px;
            .item_wrapp {
                padding: 10px;
                background: #FFFFFF;
                box-shadow: 0px 10px 60px rgba(0, 0, 0, 0.1);
                border-radius: 5px;
                transition: all .5s linear;
                &:hover {
                    color: #FFFFFF;
                    background: #00D664;
                    .item_icon,.item_title,.item_desc,a {
                        color: #FFFFFF;
                    }
                }
                .item_icon {
                    margin: 0;
                    font-size: 56px;
                    color: #00D664;
                }
                .item_title {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 44px;
                    letter-spacing: -0.01em;
                    color: #1E266D;
                    margin: 7px 0 23px;
                }
                .item_desc {
                    font-weight: 400;
                    font-size: 15px;
                    line-height: 24px;
                    letter-spacing: -0.01em;
                    color: #696969;
                }
                a {
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 24px;
                    letter-spacing: -0.01em;
                    color: #00D664;
                    text-decoration: none;
                }
            }
        }
    }
    button {
        background: #00D664;
        border-radius: 30px;
        font-weight: 500;
        font-size: 18px;
        line-height: 27px;
        color: #FFFFFF;
        padding: 14px 35px;
        border: none;
        margin-top: 70px;
        border: 1px solid #00D664;
        &:hover {
            background: transparent;
            color: #00D664;
        }
    }
}
// MENU
.menu {
    background: url('../../dist/img/menu_bg.jpg') no-repeat;
    background-size: cover;
    background-position: center;
    padding: 130px 10px 100px;

    .menu_title {
        font-weight: 500;
        font-size: 34px;
        line-height: 44px;
        letter-spacing: 0.01em;
        color: #FFFFFF;
    }
    .menu_wrapp {
        background: #FFFFFF;
        border-radius: 10px;
        padding: 0 20px 70px;
        margin-top: 50px;
        .menu_list_wrapp {
            .menu_list {
                list-style-type: none;
                border-bottom: 1px solid rgba(105, 105, 105, 0.2);
                .menu_item {
                    padding-bottom: 20px;
                    padding-top: 30px;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 24px;
                    letter-spacing: -0.01em;
                    color: #777777;
                    cursor: pointer;
                    &.active {
                        border-bottom: 3px solid #00D664;
                        color: #1E266D;
                    }
                }
            }
        }
        .content_wrapp {
            .content {
                display: none;
                &.active {
                    padding: 100px 50px 10px;
                    display: flex;
                }
                .content_left_item, .content_right_item {
                    margin-bottom: 20px;
                    .img {
                        margin-right: 18px;
                    }
                }
                .content_left_text {
                    margin: 0 20px 0 0;
                    .title {
                        margin: 0;
                        font-weight: 500;
                        font-size: 22px;
                        line-height: 24px;
                        letter-spacing: -0.01em;
                        color: #1E266D;
                        overflow: hidden;
                        max-width: 335px;
                    }
                    .desc {
                        margin: 8px 0 0;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 24px;
                        letter-spacing: -0.01em;
                        color: #696969;
                    }
                    .price {
                        p {
                            font-weight: 400;
                            font-size: 22px;
                            line-height: 26px;
                            text-align: center;
                            letter-spacing: -0.01em;
                            color: #121212;
                        }
                    }
                }
            }
        }
    }
}